import { WYNIKI_2023 } from "./wyniki_2023";

let dane = DANE;

const wszystkieRekomendacje: Rekomendacje = {};

  let nrOkregu = 1;
  okregi.forEach((okreg) => {
    const text = document.createElement("div");
    text.innerHTML = `<h2>${okreg.miasto} (${nrOkregu})</h2>`;
    text.id = `${okreg.miasto}Info`;
    text.className = "info";

    wstawRekomendowaneLogos(okreg, text);

    document.getElementById("Info").appendChild(text);
    const icon = document.getElementById(okreg.miasto + 'Icon');
    icon.addEventListener('mouseover', () => {
      text.style.display = 'block';
      document
        .getElementsByClassName("placeholder")[0]
        .setAttribute("style", "display: none");
    });
    icon.addEventListener("mouseout", () => {
      text.style.display = "none";
      if (window.innerWidth > 768) {
      document
        .getElementsByClassName("placeholder")[0]
        .setAttribute("style", "display: block");
      }
    });
    nrOkregu++;
  });
  

  window.addEventListener(
    "hashchange",
    function () {
      let element = document.getElementById(location.hash.substring(1));
      if (element) element.scrollIntoView();
    },
    false
  );

  // For initial page load:
  if (location.hash) {
      let element = document.getElementById(location.hash.substring(1));
      if (element) element.scrollIntoView();
  }

function wstawRekomendowaneLogos(okreg: Okreg, text: HTMLElement) {
  const wynikWOkregu = WYNIKI_2023[okreg.nr - 1];
  const podzialMandatow = PodzialMandatowWOkregu(wynikWOkregu, okreg, {
    log: true,
    inputWGlosach: true,
  });

  const rekomendacje = komentarze[okreg.miasto].rekomendacjePartii;
  const zaswiadczenia2019 = zaswiadczenia[okreg.nr - 1].zaswiadczenia2019;
  const zaswiadczenia2023 = zaswiadczenia[okreg.nr - 1].zaswiadczenia2023;
  const rekomendacjeHeader = document.createElement("h4");
  rekomendacjeHeader.innerHTML = `Zaświadczenia 2019/2023:<br/>`;
  rekomendacjeHeader.innerHTML += `${zaswiadczenia2019} -> ${zaswiadczenia2023} (+${Math.round(100 * (zaswiadczenia2023 / zaswiadczenia2019 - 1))}%)<br/>`;
  rekomendacjeHeader.innerHTML += `Cena mandatu: ${Math.round(podzialMandatow.cenaMandatu)} głosów<br/>`;
  rekomendacjeHeader.innerHTML += `Zaświadczenia / cena mandatu: ${Math.round(1000 * zaswiadczenia2023 / podzialMandatow.cenaMandatu) / 10}%`;
  text.appendChild(rekomendacjeHeader);
  let color = "orange";
  if (zaswiadczenia2023 / zaswiadczenia2019 > 3) {
    color = "red";
  }
  if (zaswiadczenia2023 / zaswiadczenia2019 < 2) {
    color = "green";
  }
  document.getElementById(okreg.miasto + "Icon").style.fill = color;
  const komentarz = document.createElement("p");
  komentarz.innerHTML = `<i>${komentarze[okreg.miasto].txt || ""}</i>`;
  text.appendChild(komentarz);
  const koncowy = document.createElement("p");
  koncowy.innerHTML = `${komentarzKoncowy[okreg.nr - 1] || ""}`;
  text.appendChild(koncowy);
}

